export const dynamic = "force-dynamic";

type Todo = {
  _id: string;
  title: string;
  description?: string;
  date: string;
};

async function getTodos(page = 1): Promise<{ todos: Todo[] }> {
  // Ensure you're calling the correct backend URL
  const res = await fetch(`http://localhost:5000/api/todos?page=${page}`, {
    cache: "no-store",
  });

  if (!res.ok) {
    throw new Error("Failed to fetch todos");
  }

  return res.json();
}

export default async function TodoPage() {
  const { todos } = await getTodos();

  return (
    <div className="flex h-screen">
      {/* Left: Sidebar */}
      <div className="w-1/3 bg-white p-6 border-r overflow-y-auto">
        <h1 className="text-2xl font-bold mb-4">To-Do</h1>

        <form action="/api/todos" method="POST" className="space-y-4 mb-6">
          <input
            name="title"
            placeholder="Title"
            className="w-full p-2 border rounded"
          />
          {todos.map((todo: Todo) => (
            <button
              type="submit"
              className="bg-blue-500 text-white px-4 py-2 rounded"
            >
              Add Task
            </button>
          ))}
        </form>

        <div className="space-y-4">
          {todos.map((todo) => (
            <a
              key={todo._id}
              href={`/todo/${todo._id}`}
              className="block p-4 bg-gray-50 rounded shadow"
            >
              <h2 className="font-semibold">{todo.title}</h2>
              <p className="text-sm text-gray-500">
                {new Date(todo.date).toLocaleDateString()}
              </p>
            </a>
          ))}
        </div>
      </div>

      {/* Right: Empty */}
      <div className="flex-1 p-6 text-center text-gray-400">
        <p>Select a To-Do to view</p>
      </div>
    </div>
  );
}
